.pre-listening-container {
    width: 100%;
    max-width: 800px;
    margin: 3rem auto;
    padding: 2rem;
    background: rgba(30, 30, 30, 0.9); /* Dark background */
    border-radius: 10px;
    box-shadow: 0 4px 8px rgba(0, 0, 0, 0.5);
    text-align: center;
    color: #fff; /* Light text color */
  }
  
  .questionnaire-form {
    display: flex;
    flex-direction: column;
  }
  
  .question-card {
    background: rgba(50, 50, 50, 0.9); /* Slightly lighter dark background */
    border-radius: 10px;
    padding: 1rem;
    margin: 0.5rem 0;
    text-align: left;
  }
  
  .question-card label {
    display: block;
    margin-bottom: 0.5rem;
    color: #ccc; /* Light gray color for labels */
  }
  
  .question-card input[type="radio"] {
    margin-left: 0.5rem;
  }
  
  .btn-primary {
    padding: 10px 20px;
    background-color: #0066ff;
    color: white;
    border: none;
    border-radius: 5px;
    cursor: pointer;
    margin-top: 20px;
    transition: background-color 0.3s ease;
  }
  
  .btn-primary:hover {
    background-color: #0056d1;
  }
  
  h2 {
    color: #fff; /* Light color for headings */
  }
  
  @media (max-width: 991px) {
    .pre-listening-container {
      padding: 1rem;
    }
  }